<template>
  <div>

    <table class="table m-0 table-top-line-none">

      <tbody>
        <tr class="text-left" v-for="(v, index) in prices" :key="v.id">
          <td width="6%" class="text-accent">{{ index + 1 }}</td>
          <td width="10%">{{ v.num }}</td>
          <td width="20%">{{ v.membery.num }}&nbsp;{{ v.membery.named }}</td>
          <td width="15%">
            {{ ser_timed(v.start_time) }}
            <div 
              class="tip-wrapper tip-pos-fix animate-shake hand" 
              v-html="ser_timed_now_month(v.start_time, v.id)"
              @click="showTip"
            >
            </div>
            
          </td>
          <td width="12%" class="text-center">{{ v.pay_time.named }}</td>
          <td width="10%" class="text-center">{{ v.freight_num }}</td>
          <td width="10%" class="text-center hand" @click="pdfView(v.id)">查看</td>
          
          <td width="12%" class="text-center">

            <i @click="option(v.id, 'trash')" class="float-right">
              <remove></remove>
            </i>

            <i @click="option(v.id, 'update')" class="float-right">
              <pencil></pencil>
            </i>

          </td>
        </tr>
      </tbody>

    </table>

    <custom-modal @done_Father="done" :need_cancel="true" :title="`提示`" :txt="`按修改立即轉到修改頁面`" :id_prefix="`pricesUpdateSure`">
      <div slot="submit_text">立即跳轉</div>
    </custom-modal>
  </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'

import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'
import CustomModal from '@/components/Modal/CustomModal.vue'

export default {
  name: 'PricesViewBody',
  components: { Pencil, Remove, CustomModal },
  props: {
    prices: {
      type: Array
    }
  },
  data() {
    return {
      activeId: 0
    }
  },
  methods: {
    ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
    ser_timed_now_month(v, id) { 
      const res = tool.timed.is_now_month(v)
      
      if (res) {
        // return `<div class="tip tip-success fw-b">!</div>`
        return ''
      } 
      return ''
    },
    option(id, option) { 
      this.activeId = id
      if (option == 'update') {
        // tool.showModal('pricesUpdateSureModal')
        this.done()
        return 
      }
      this.$emit('option_Father' , id, option) 
    },
    pdfView(id) { this.$emit('pdfView_Father', id) },

    showTip() {
      tool.showToast('該報價單是屬於今月份的。')
    },

    done() {
      this.$emit('option_Father' , this.activeId, 'update') 
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/animate.sass"

</style>


